import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';
import { Teacher } from './teacher';
import {Params} from '@angular/router';
const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};
@Injectable({
  providedIn: 'root'
})
export class TeacherService {

  private teacherUrl = '../teacher.ctl';  // sevlet的相对路径，
  constructor(private http: HttpClient) { }

  /** GET colleges from the server */
  getTeachers(): Observable<Teacher[]> {
    return this.http.get<any>(this.teacherUrl + '')
      .pipe(
        catchError(this.handleError<Teacher[]>('getTeachers', []))
      );
  }
  /** GET college by id. Will 404 if id not found */
  getTeacher(id: number): Observable<Teacher> {
    const url = this.teacherUrl + '?id=' + id;
    return this.http.get<Teacher>(url).pipe(
      catchError(this.handleError<Teacher>(`getTeacher id=${id}`))
    );
  }

  /** POST: add a new college to the server */
  addTeacher(teacher: { no: string; name: string; profTitle: { id: number; no: string; description: string; remarks: string }; degree: { id: number; no: string; description: string; remarks: string }; departemnt: { id: number; no: string; description: string; remarks: string } }): Observable<Teacher> {
    return this.http.post<Teacher>(this.teacherUrl + '', teacher, httpOptions).pipe(
      catchError(this.handleError<Teacher>('addTeacher'))
    );
  }

  /** DELETE: delete the college from the server */
  deleteTeacher(teacher: Teacher | number): Observable<Teacher> {
    const id = typeof teacher === 'number' ? teacher : teacher.id;
    return this.http.delete<any>(this.teacherUrl + '?id=' + id, httpOptions).pipe(
      catchError(this.handleError<Teacher>('deleteTeacher'))
    );
  }

  /** PUT: update the hero on the server */
  updateTeacher(teacher: Teacher): Observable<any> {
    return this.http.put(this.teacherUrl + '', teacher, httpOptions).pipe(
      catchError(this.handleError<any>('updateTeacher'))
    );
  }

  /**
   * Handle Http operation that failed.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error); // log to console instead
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}

