import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { DepartmentService } from '../department.service';
import {School} from '../school';
@Component({
  selector: 'app-add-department',
  templateUrl: './addDepartment.component.html',
  styleUrls: ['./addDepartment.component.css']
})
export class AddDepartmentComponent implements OnInit {
  @Input() department: { no: string; description: string; remarks: string; school: { id: number; no: string; description: string; remarks: string; school: School }};
  constructor(
    private route: ActivatedRoute,
    private departmentService: DepartmentService,
    private location: Location
  ) {}

  ngOnInit(): void {}
  save(description: string, no: string, remarks: string, school: {id: number, description: string, no: string, remarks: string }): void { //多个文本框数据
    // @ts-ignore
    this.department = {no: no.trim(), description: description.trim(), remarks: remarks.trim(), school: { school: id.trim(), school: no.trim(), school: description.trim(), school: remarks.trim()}};
    if (!description) { return; }
    this.departmentService.addDepartment(this.department)
      .subscribe(() => this.goBack());
  }
  goBack(): void {
    this.location.back();
  }
}
