import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import { TeacherService } from '../teacher.service';
@Component({
  selector: 'app-add-teacher',
  templateUrl: './addTeacher.component.html',
  styleUrls: ['./addTeacher.component.css']
})
export class AddTeacherComponent implements OnInit {
  @Input() teacher: { no: string; name: string; profTitle: { id: number; no: string; description: string; remarks: string}; degree: {id: number; no: string; description: string; remarks: string}; departemnt: { id: number; no: string; description: string; remarks: string}};
  constructor(
    private route: ActivatedRoute,
    private teacherService: TeacherService,
    private location: Location
  ) {}

  ngOnInit(): void {}

  save(no: string, name: string, profTitle: { id: number, no: string, description: string, remarks: string}, degree: {id: number, no: string, description: string, remarks: string}, department: { id: number, no: string, description: string, remarks: string}): void { //多个文本框数据
    // @ts-ignore
    this.teacher = {no: no.trim(), name: name.trim(), profTitle: { profTitle: no.trim(), profTitle: description.trim(), profTitle: remarks.trim()}, degree: { degree: no.trim(), degree: description.trim(), degree: remarks.trim()}, department: { department: no.trim(), department: description.trim(), department: remarks.trim()}};
    if (!name) { return; }
    this.teacherService.addTeacher(this.teacher)
      .subscribe(() => this.goBack());
  }
  goBack(): void {
    this.location.back();
  }
}
